<h1 id="linkedlist" tabindex="-1"><a class="header-anchor" href="#linkedlist" aria-hidden="true">#</a> LinkedList</h1>
<p>基于<code>List</code>接口和<code>Deque</code>接口所实现的<code>双端链表</code>的集合.实现所有可选列表操作,并允许所有元素(包括空<code>null</code>)</p>
<p><strong>需要注意的是,LinkedList并不是同步的</strong>,也就是说它不是线程安全的..如果多线程同时操作一个LinkedList,并且至少一个线程修改了集合的结构,它必须在外部同步(结构上的修改指的是任何操作,例如添加一个或多个元素,删除一个或多个元素;)</p>
<p>从类注释上可以的到几点信息</p>
<ol>
<li>
<p>LinkedList允许存<code>NULL</code></p>
